import React from 'react';
import { Form, Button } from 'react-bootstrap';
import {USERS_TYPES_DATA} from '../../constants/users-data'
const FormRegistration = ()=>{

    return(
        <Form className="container">
            <Form.Group>
                <Form.Label>{USERS_TYPES_DATA.FIRST_NAME}</Form.Label>
                <Form.Control type="text" placeholder={`${USERS_TYPES_DATA.FIRST_NAME.toLowerCase()}`} />
                <Form.Text className="text-muted">
                </Form.Text>
            </Form.Group>

            <Form.Group>
                <Form.Label>{USERS_TYPES_DATA.LAST_NAME}</Form.Label>
                <Form.Control type="text" placeholder={`${USERS_TYPES_DATA.LAST_NAME.toLowerCase()}`} />
                <Form.Text className="text-muted">
                </Form.Text>
            </Form.Group>

            <Form.Group>
                <Form.Label>{USERS_TYPES_DATA.GENDER}</Form.Label>
                <Form.Control as="select">
                    <option>Choose...</option>
                    <option>male</option>
                    <option>female</option>
                </Form.Control>
            </Form.Group>

            <Form.Group>
                <Form.Label>{USERS_TYPES_DATA.AGE}</Form.Label>
                <Form.Control type="number"
                              placeholder={`${USERS_TYPES_DATA.AGE.toLowerCase()}`}
                              min={0}
                />
                <Form.Text className="text-muted">
                </Form.Text>
            </Form.Group>


            <Button variant="primary" type="submit">
                Submit
            </Button>
        </Form>
    )
};

export default FormRegistration;